@mixin tunnel__box {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  svg * {
    fill: #8306b1;
  }
  svg {
    filter: drop-shadow(1px 0px 12px #8306b1);
    -webkit-filter: drop-shadow(1px 0px 12px #8306b1);
    -moz-filter: drop-shadow(1px 0px 12px #8306b1);
  }
}

.tunnel {
  width: 100%;
  height: 1500px;
  background-color: black;
}
.tunnel__text {
  color: white;
}
.tunnel__sticky {
  background-color: black;
  height: 100vh;
  width: 100%;
  position: sticky;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.tunnel__sticky-inner {
  width: calc(100% - 40px);
  height: calc(100% - 40px);
  position: relative;
}
.tunnel__box__lt {
  @include tunnel__box;
  .spotlight__inner__light__bg:after {
    width: 100%;
    height: 100%;
    background: linear-gradient(
      to right,
      #8306b1 -89%,
      rgba(255, 255, 255, 0) 70%
    );
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;

    height: 100%;
    @media (min-width: 500px) {
      background: linear-gradient(
        to right,
        #8306b1 -89%,
        rgba(255, 255, 255, 0) 95%
      );
    }
  }
  .spotlight__inner__light {
    clip-path: polygon(0 130px, 100% 0, 100% 100%, 0 171px);
    position: absolute;
    left: 48px;
  }
  .spotlight__inner {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }
}
.spotlight__inner__light {
  width: 300px;
  height: 300px;
}
.spotlight__inner__light__bg {
  width: 100%;
  height: 100%;

  position: relative;
}

.tunnel__box__rt {
  @include tunnel__box;
  .spotlight__inner__light {
    clip-path: polygon(0 0, 100% 130px, 100% 171px, 0 100%);
    position: absolute;
    left: -300px;
  }
  svg {
    transform: rotate(180deg);
  }
  .spotlight__inner {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row-reverse;
  }
  .spotlight__inner__light__bg:after {
    width: 100%;
    height: 100%;
    background: linear-gradient(
      to left,
      #8306b1 -89%,
      rgba(255, 255, 255, 0) 70%
    );
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;

    height: 100%;

    @media (min-width: 500px) {
      background: linear-gradient(
        to left,
        #8306b1 -89%,
        rgba(255, 255, 255, 0) 95%
      );
    }
  }
}

.red {
  background-color: red;
  height: 20px;
  width: 100%;
}
.boxes {
  width: 100%;
  height: 40px;
  background-color: red;
  margin-bottom: 8px;
}
